
.list { 
	display: -ms-flexbox; 
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

@media only screen and (min-width: 1001px) {

	.list {
		width: 900px;
	}
}

@media only screen and (max-width: 1000px) {
	
	.container {
		-ms-flex-preferred-size: 60%;
		    flex-basis: 60%;
		-ms-flex-negative: 2;
		    flex-shrink: 2;
	}

	.item {
		margin: 0 10px 20px 10px;
	}
}


