
.top { 
	background-color: #fff;
	height: 270px;
	border-radius: 5px;
	-webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
	        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column;
	    flex-flow: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 200px;
}

.top:hover {
	-webkit-box-shadow: 0 6px 12px rgba(0,0,0,0.16), 0 6px 12px rgba(0,0,0,0.23);
	        box-shadow: 0 6px 12px rgba(0,0,0,0.16), 0 6px 12px rgba(0,0,0,0.23);
	background-color: rgb(249, 255, 217);
}

.cover,
.keg-cover {
	background-position: center;
	background-repeat: no-repeat;
	margin-top: 15px;
}
.cover {
	background-size: 35%;
}

.keg-cover {
	background-size: 60%;
}

.title {
	white-space: nowrap;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	color: #fd8e04;
	margin-top: 10px;
	width: calc(100% - 10px);
	text-align: center;
	font-size: 1.1em;
}

.slogan {
	white-space: nowrap;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	color: #333;
	margin-bottom: 10px;
	text-align: center;
	width: calc(100% - 10px);
	font-size: 0.8em;
}

@media only screen and (min-width: 1001px) {
	.preview-top {
		width: 200px;		
	}
}


